import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const Store = new Vuex.Store({
  state: {
    address: null,
    balance: 0,
    staking: 0,
    reward: 0,
    total_reward: 0,
    isApproved: false,
    claimedReward: 0,
    unClaimedReward: 0,
    liquidityFund: 0,
    treasuryFund: 0,
    fundThreshold: 0,
    referred: null,
    userRef: window.location.host,
    refPending: null,
    referrerCount: 0,
    referredClaims: 0,
    rateReferral: 0,
    requireFund: 0,
    userData: [],
    rateReward: 0,
    excludeReward: false,
    canCompound: 0,
    compoundBalance: 0,
    compoundingBalance: 0,
    compoundDelay: 0,
    massCompoundReward: 0,
    rateCompound: 0,
    compoundStatus: 0,
    claimStatus: false,
    rewardable: false,

  },
  getters: {
    address: (state) => state.address,
    balance: (state) => state.balance,
    staking: (state) => state.staking,
    reward: (state) => state.reward,
    total_reward: (state) => state.total_reward,
    isApproved: (state) => state.isApproved,
    claimedReward: (state) => state.claimedReward,
    unClaimedReward: (state) => state.unClaimedReward,
    liquidityFund: (state) => state.liquidityFund,
    treasuryFund: (state) => state.treasuryFund,
    fundThreshold: (state) => state.fundThreshold,
    referred: (state) => state.referred,
    userRef: (state) => state.userRef,
    refPending: (state) => state.refPending,
    referrerCount: (state) => state.referrerCount,
    referredClaims: (state) => state.referredClaims,
    rateReferral: (state) => state.rateReferral,
    requireFund: (state) => state.requireFund,
    userData: (state) => state.userData,
    rateReward: (state) => state.rateReward,
    excludeReward: (state) => state.excludeReward,
    canCompound: (state) => state.canCompound,
    compoundBalance: (state) => state.compoundBalance,
    compoundingBalance: (state) => state.compoundingBalance,
    compoundDelay: (state) => state.compoundDelay,
    massCompoundReward: (state) => state.massCompoundReward,
    rateCompound: (state) => state.rateCompound,
    compoundStatus: (state) => state.compoundStatus,
    claimStatus: (state) => state.claimStatus,
    rewardable: (state) => state.rewardable,
  },
  actions: {
  },
  mutations: {
    SET(state, [key, value]) {
      state[key] = value;
    }
  }
})

export default Store;