<template>
  <div class="main-container" id="app">
    <div id="header">
      <img id="logo" alt="Perks Protocol" src="img/logo.png" />
      <media :query="{ minWidth: 516 }">
        <div v-if="!address" id="connectWallet" @click="connect">Connect</div>
        <div v-else id="connectWallet" @click="disconnect">
          Disconnect
          <div class="address">{{ formatAddress(address) }}</div>
        </div>
      </media>
      <media :query="{ maxWidth: 515 }">
        <div v-if="!address" id="connectWallet" @click="menuOpen = !menuOpen">
          <i class="fa fa-bars"></i>
        </div>
        <div v-else id="connectWallet" @click="menuOpen = !menuOpen">
          <i class="fa fa-bars"></i>
          <div class="address">{{ formatAddress(address) }}</div>
        </div>
      </media>

      <div v-if="menuOpen" class="mobile-menu">
        <div
          @click="
            active = 0;
            menuOpen = !menuOpen;
          "
        >
          Overview
        </div>

        <div
          @click="
            active = 1;
            menuOpen = !menuOpen;
          "
          v-if="address"
        >
          Your Reward
        </div>

        <div
          @click="
            active = 2;
            menuOpen = !menuOpen;
          "
          v-if="address"
        >
          Earn Reward
        </div>

        <div
          @click="
            active = 3;
            menuOpen = !menuOpen;
          "
          v-if="address"
        >
          Referral Reward
        </div>

        <div
          @click="
            active = 5;
            menuOpen = !menuOpen;
          "
        >
          About PERKS
        </div>

        <div v-if="!address" @click="connect">Connect Wallet</div>
        <div v-else @click="disconnect">Disconnect Wallet</div>
      </div>

      <div v-if="menuOpen" class="menuBack" @click="menuOpen = false"></div>
    </div>

    <div id="content" ref="content">
      <div id="tabs">
        <div class="tabs">
          <div
            class="tab content-trigger"
            v-bind:class="active == 0 ? 'active' : null"
            data-trigger="overview"
            @click="active = 0"
          >
            Overview
          </div>

          <div
            v-if="address"
            id="stake"
            class="tab content-trigger connected"
            data-trigger="stake"
            v-bind:class="active == 1 ? 'active' : 'null'"
            @click="active = 1"
          >
            Your Reward
          </div>

          <div
            v-if="address"
            id="harvest"
            class="tab content-trigger connected"
            data-trigger="harvest"
            v-bind:class="active == 2 ? 'active' : 'null'"
            @click="active = 2"
          >
            Earn Reward
          </div>

          <div
            v-if="address"
            id="referral"
            class="tab content-trigger connected"
            data-trigger="referral"
            v-bind:class="active == 3 ? 'active' : 'null'"
            @click="active = 3"
          >
            Referral Reward
          </div>

          <div
            class="tab content-trigger"
            data-trigger="info"
            v-bind:class="active == 5 ? 'active' : 'null'"
            @click="active = 5"
          >
            About PERKS
          </div>
        </div>

        <div class="tabs-mobile">
          <div
            class="tab content-trigger"
            v-bind:class="active == 0 ? 'active' : null"
            data-trigger="overview"
            v-if="active == 0"
          >
            Overview
          </div>

          <div
            id="stake"
            class="tab content-trigger connected"
            data-trigger="stake"
            v-bind:class="active == 1 ? 'active' : 'null'"
            v-if="active == 1"
          >
            Your Reward
          </div>

          <div
            id="harvest"
            class="tab content-trigger connected"
            data-trigger="harvest"
            v-bind:class="active == 2 ? 'active' : 'null'"
            v-if="active == 2"
          >
            Earn Reward
          </div>

          <div
            id="referral"
            class="tab content-trigger connected"
            data-trigger="referral"
            v-bind:class="active == 3 ? 'active' : 'null'"
            v-if="active == 3"
          >
            Referral Reward
          </div>

          <div
            class="tab content-trigger"
            data-trigger="info"
            v-bind:class="active == 5 ? 'active' : 'null'"
            v-if="active == 5"
          >
            About PERKS
          </div>
        </div>

        <div class="content">
          <div
            id="overview_content"
            class="tab_content"
            v-bind:class="active == 0 ? 'active' : 'null'"
          >
            <div class="connected">
              <h3 class="earn-reward">
                Welcome to Perks Protocol reward dashboard.
              </h3>
              <div class="overview">
                <div class="stats">
                  <span class="overview-perks">Fund Reward</span>
                  <span class="overview-price"
                    >{{ numberFormat(fundingReward()) }} PERKS</span
                  >
                </div>

                <div class="stats">
                  <span class="overview-perks">Compound Reward</span>
                  <span class="overview-price">
                    {{ numberFormat(formatBalance(massCompoundReward)) }}
                    PERKS</span
                  >
                </div>

                <div class="stats">
                  <span class="overview-perks">Total Claimed</span>
                  <span class="overview-price"
                    >{{
                      numberFormat(formatBalance(claimedReward))
                    }}
                    PERKS</span
                  >
                </div>

                <div class="stats">
                  <span class="overview-perks">Total Unclaimed</span>
                  <span class="overview-price"
                    >{{
                      numberFormat(formatBalance(unClaimedReward))
                    }}
                    PERKS</span
                  >
                </div>

                <div class="stats">
                  <span class="overview-perks">Liquidity Reserve</span>
                  <span class="overview-price"
                    >{{
                      numberFormat(formatBalance(liquidityFund))
                    }}
                    PERKS</span
                  >
                </div>

                <div class="stats">
                  <span class="overview-perks">Treasury Reserve</span>
                  <span class="overview-price"
                    >{{ numberFormat(formatBalance(treasuryFund)) }} PERKS</span
                  >
                </div>
              </div>
              <div v-if="!address" class="su-msg">
                <span
                  id="connect-wallet"
                  class="connect-trigger trigger-button"
                  @click="connect"
                  >CONNECT</span
                >
              </div>
            </div>
          </div>
          <div
            id="stake_content"
            class="tab_content"
            v-bind:class="active == 1 ? 'active' : 'null'"
          >
            <div class="connected">
              <h3 class="earn-reward">Veiw and claim your PERKS reward.</h3>

              <div class="funding-summary">
                <p class="earn-process">Your Balance</p>
                <p>
                  The available balance of PERKS in your wallet, including total
                  claimed and lost rewards.
                </p>
                <p class="item-list">
                  <span class="title-perks">Wallet Balance</span>
                  <span class="price-perks"
                    >{{ numberFormat(formatBalance(balance)) }} PERKS</span
                  >
                </p>
                <p class="item-list">
                  <span class="title-perks">Claimed Reward</span>
                  <span class="price-perks"
                    >{{
                      numberFormat(formatBalance(userData?.claimedReward))
                    }}
                    PERKS</span
                  >
                </p>
                <p class="item-list">
                  <span class="title-perks">Lost Reward</span>
                  <span class="price-perks"
                    >{{
                      numberFormat(formatBalance(userData?.lostReward))
                    }}
                    PERKS</span
                  >
                </p>
                <div class="button-process">
                  <button
                    id="harvest_confirm"
                    class="action_confirm"
                    v-bind:class="[
                      (requireFund === 0 && !excludeReward) || isLoadingFund
                        ? 'buy-more-disabled'
                        : '',
                    ]"
                    :disabled="
                      (requireFund === 0 && !excludeReward) || isLoadingFund
                    "
                    @click="buy"
                  >
                    <span>Buy PERKS</span>
                    <div v-if="isLoadingFund" class="spinner"></div>
                  </button>
                </div>
              </div>
              <div class="funding-summary">
                <p class="earn-process">Pending Reward</p>
                <p>
                  Your unclaimed and accumulating reward balance. When you
                  claim, you transfer your rewards to your wallet balance.
                </p>
                <p class="item-list">
                  <span class="title-perks">Unclaimed Reward</span>
                  <span class="price-perks"
                    >{{
                      numberFormat(formatBalance(userData?.rewardBalance))
                    }}
                    PERKS</span
                  >
                </p>
                <div class="button-process">
                  <button
                    id="harvest_confirm"
                    class="action_confirm"
                    v-bind:class="[
                      !claimButtonStatus ? 'buy-more-disabled' : '',
                    ]"
                    :disabled="!claimButtonStatus"
                    @click="claim"
                  >
                    <span v-if="claimCountDownText">{{
                      claimCountDownText
                    }}</span>

                    <span v-if="!claimCountDownText">{{
                      toHHMMSS(claimCountDown)
                    }}</span>

                    <div v-if="isLoadingFund" class="spinner"></div>
                  </button>
                </div>
                <p class="item-list">
                  <span class="title-perks">HODL Compounding</span>
                  <span class="price-perks"
                    >{{ numberFormat(compoundingBalance) }} PERKS</span
                  >
                </p>
                <div class="button-process">
                  <button
                    id="harvest_confirm"
                    class="action_confirm"
                    v-bind:class="[
                      compoundCountDown !== 0 ||
                      isLoadingFund ||
                      (canCompound == '0' && compoundBalance == '0')
                        ? 'buy-more-disabled'
                        : '',
                    ]"
                    :disabled="
                      compoundCountDown !== 0 ||
                      isLoadingFund ||
                      (canCompound == '0' && compoundBalance == '0')
                    "
                    @click="compounduser"
                  >
                    <span v-if="compoundCountDownText">{{
                      compoundCountDownText
                    }}</span>

                    <span v-else>{{ toHHMMSS(compoundCountDown) }}</span>
                    <div v-if="isLoadingFund" class="spinner"></div>
                  </button>
                </div>
              </div>
            </div>
          </div>

          <div
            id="harvest_content"
            class="tab_content"
            v-bind:class="active == 2 ? 'active' : 'null'"
          >
            <div class="connected">
              <h3 class="earn-reward">
                Earn PERKS by processing project funding.
              </h3>
              <div class="funding-summary">
                <p class="earn-process">Project Fund</p>
                <p>
                  Process liquidity and generate marketing funds from PERKS
                  reserves to earn a reward.
                </p>
                <p class="item-list">
                  <span class="title-perks">Fund Reward</span>
                  <span class="price-perks"
                    >{{ numberFormat(fundingReward()) }} PERKS</span
                  >
                </p>
                <div class="button-process">
                  <button
                    id="harvest_confirm"
                    class="action_confirm"
                    v-bind:class="[
                      !processButtonStatus ? 'buy-more-disabled' : '',
                    ]"
                    :disabled="!processButtonStatus"
                    @click="processfund"
                  >
                    <span v-if="processCountDownText">{{
                      processCountDownText
                    }}</span>
                    <span v-if="!processCountDownText">{{
                      toHHMMSS(processCountDown)
                    }}</span>
                    <div v-if="isLoadingFund" class="spinner"></div>
                  </button>
                </div>
              </div>

              <div class="funding-summary">
                <p class="earn-process">Mass Compound</p>
                <p>
                  Process mass compounding for all users to earn a reward. You
                  will receive a percentage of all users' compounded
                  transactions.
                </p>
                <p class="item-list">
                  <span class="title-perks">Compound Reward</span>
                  <span class="price-perks"
                    >{{
                      numberFormat(formatBalance(massCompoundReward))
                    }}
                    PERKS</span
                  >
                </p>

                <div class="button-process">
                  <button
                    id="harvest_confirm"
                    class="action_confirm"
                    v-bind:class="[
                      (!rewardable && !excludeReward) ||
                      massCompoundReward == 0 ||
                      isLoadingFund
                        ? 'buy-more-disabled'
                        : '',
                    ]"
                    :disabled="
                      (!rewardable && !excludeReward) ||
                      massCompoundReward == 0 ||
                      isLoadingFund
                    "
                    @click="massCompound"
                  >
                    <span>Compound</span>
                    <div v-if="isLoadingFund" class="spinner"></div>
                  </button>
                </div>
              </div>

              <div class="funding-summary">
                <p class="earn-process">Fund Summary</p>

                <p>
                  PERKS reserves and funding requirements for liquidity and
                  marketing fund generation.
                </p>

                <p class="item-list">
                  <span class="title-perks">Fund Threshold</span>
                  <span class="price-perks"
                    >{{ numberFormat(formatBalance(fundThreshold)) }} BNB</span
                  >
                </p>
                <p class="item-list">
                  <span class="title-perks">Required Fund</span>
                  <span class="price-perks"
                    >{{ numberFormat(formatBalance(requireFund)) }} PERKS</span
                  >
                </p>
                <p class="item-list">
                  <span class="title-perks">Liquidity Reserve</span>
                  <span class="price-perks"
                    >{{
                      numberFormat(formatBalance(liquidityFund))
                    }}
                    PERKS</span
                  >
                </p>
                <p class="item-list">
                  <span class="title-perks">Treasury Reserve</span>
                  <span class="price-perks"
                    >{{ numberFormat(formatBalance(treasuryFund)) }} PERKS</span
                  >
                </p>
              </div>
            </div>
          </div>
          <div
            id="referral_content"
            class="tab_content"
            v-bind:class="active == 3 ? 'active' : 'null'"
          >
            <div class="connected">
              <h3 class="earn-reward">Earn PERKS by referring your friends.</h3>

              <div class="funding-summary" v-if="refPending">
                <p class="earn-process">Pending Referral</p>
                <p>
                  You were referred by the referrer address below. Please accept
                  this request to receive your referral bonus or reject to
                  cancel.
                </p>
                <p class="item-list">
                  <span class="title-perks">Referrer</span>
                  <span class="price-perks">{{
                    formatAddress(refPending)
                  }}</span>
                </p>
                <p class="item-list">
                  <span class="title-perks">Referral Bonus</span>
                  <span class="price-perks"
                    >{{ numberFormat(formatBalance(rateReferral)) }} PERKS</span
                  >
                </p>
                <div class="button-process">
                  <button
                    class="action_confirm referral-cancel"
                    @click="rejectRef"
                  >
                    Reject
                  </button>

                  <button
                    class="action_confirm referral-accept"
                    v-bind:class="[isLoadingFund ? 'buy-more-disabled' : '']"
                    :disabled="isLoadingFund"
                    @click="acceptRef"
                  >
                    Accept
                    <div v-if="isLoadingFund" class="spinner"></div>
                  </button>
                </div>
              </div>

              <div class="funding-summary">
                <p class="earn-process">Your Referral Link</p>
                <p>
                  Share your referral link or code with your friends. Once they
                  accept it, you will receive the same claim amount as they do
                  every time they claim.
                </p>

                <p class="item-list">
                  <span class="title-perks ref-link" @click="copyRef">
                    https://reward...app?ref={{ formatAddress(address) }}
                    <i class="fa fa-copy refLink" @click="copyRef"></i>
                    <span class="tooltiptext" :class="{ show: showTip }"
                      >Copied!</span
                    >
                  </span>
                </p>

                <p class="earn-process">Your Referral Code</p>
                <p class="item-list item-qr">
                  <span class="qr-img">
                    <qr-code
                      :text="userRef"
                      color="#fff"
                      bg-color="#000"
                      error-level="L"
                    >
                    </qr-code>
                  </span>
                </p>
                <p class="item-list">
                  <span class="title-perks">Total Referrals</span>
                  <span class="price-perks">{{
                    numberFormat(referrerCount)
                  }}</span>
                </p>
                <p class="item-list">
                  <span class="title-perks">Referral Reward</span>
                  <span class="price-perks"
                    >{{
                      numberFormat(formatBalance(referredClaims))
                    }}
                    PERKS</span
                  >
                </p>
                <p class="item-list">
                  <span class="title-perks">Your Referrer</span>
                  <span class="price-perks">{{ referred }} </span>
                </p>
              </div>
            </div>
          </div>

          <div
            id="info_content"
            class="tab_content"
            v-bind:class="active == 5 ? 'active' : 'null'"
          >
            <div class="about"><img alt="Perks Protocol: DeFi reDeFined" src="img/defi.png" /></div>

            <h3 class="earn-reward">
              No taxes, no fees, no bots - Only PERKS!
            </h3>

            <div class="reward-div">
              <p class="earn-title">
                Perks Protocol is an innovative DeFi launchpad that is designed
                to reward and incentivize users, traders, developers, holders,
                and investors massively in order to promote engagement,
                partnerships, and adoption. Every time you buy
                PERKS, or execute transactions transactions listed below, you will receive
                additional PERKS reward.
              </p>
            </div>

            <div class="reward-div">
              <p class="earn-title">BUY PERKS</p>
              <p>
                You can earn rewards when you buy PERKS tokens. After each
                purchase, you can view your unclaimed reward balance
                <span class="text-connect" v-if="!address" @click="connect">
                  after you <b><u>CONNECT</u></b> your wallet</span
                >
                under the <strong>'Your Reward'</strong> tab. <br /><br />There
                is a 24-hour claim interval after each purchase transaction. If
                you sell your PERKS tokens or transfer them before this
                interval, you will forfeit all your rewards.<br /><br />However,
                if you buy more PERKS or <strong>Process Funding</strong>, your
                unclaimed reward balance will be automatically compounded.<br />
                <br />Please check the claim timer under the
                <strong>Your Reward</strong> tab before selling or transferring
                your PERKS tokens to another address.
              </p>
            </div>

            <div class="reward-div">
              <p class="earn-title">EARN REWARD</p>
              <p>
                You can also earn more rewards by helping with liquidity,
                treasury and mass compounding processing. <br /><br />Perks Protocol has opened up these processes
                to any user willing to execute these transactions. The user will
                be automatically rewarded with a percentage of the transaction
                total amount.<br /><br />Go to the
                <strong>Earn Reward</strong> tab to get started
                <i v-if="address">.</i>
                <span class="text-connect" v-if="!address" @click="connect">
                  after you <b><u>CONNECT</u></b> your wallet.</span
                ><br /><br />Please note that there is also a 24-hour reward claiming
                interval for these transaction.
              </p>
            </div>

            <div class="reward-div">
              <p class="earn-title">REFERRAL REWARD</p>
              <p>
                You can earn PERKS reward by referring your friends.
                <br /><br />Your referred friend will receive a bonus if they
                accept your referral.<br /><br />Subsequently, anytime they
                claim their reward, you will equally receive the same claim
                amount from their transaction.<br /><br />Go to the
                <strong>Referral Reward</strong> tab to get started
                <i v-if="address">.</i>
                <span class="text-connect" v-if="!address" @click="connect">
                  after you <b><u>CONNECT</u></b> your wallet.</span
                ><br /><br />You can refer as many friends as possible.<br /><br />Please
                note that there is also a 24-hour claiming interval for this
                transaction.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
    <alert-modal :active="ModalActive" @ok="ModalActive = false" />
  </div>
</template>

<script>
// @ is an alias to /src
import Footer from "@/views/Footer";
import { mapGetters } from "vuex";
import Web3 from "@/helpers/Web3";
import Observer from "@/helpers/Observer";
import Utils from "@/helpers/Utils";
import AlertModal from "@/components/AlertModal.vue";
import { E_REJECT_SIGN, E_SIGNED } from "@/constants/events";
import Media from "vue-media";
import dateFormat from "dateformat";
import { ref } from "vue";
import Vue from "vue";
import VueQRCodeComponent from "vue-qrcode-component";

Vue.component("qr-code", VueQRCodeComponent);

export default {
  name: "Home",
  components: {
    Footer,
    Media,
    AlertModal,
  },
  beforeDestroy() {
    Observer.$off(E_REJECT_SIGN);
  },

  methods: {
    countDownTimer() {
      if (this.processTimer) {
        clearInterval(this.processTimer);
        this.processCountDown = 0;
      }

      if (this.claimTimer) {
        clearInterval(this.claimTimer);
        this.claimCountDown = 0;
      }

      if (this.compoundTimer) {
        clearInterval(this.compoundTimer);
        this.compoundCountDown = 0;
      }

      const handlerProceess = () => {
        this.processCountDownText = null;
        this.processCountDown =
          new Date(this.userData.nextProcess * 1000) - new Date().getTime();

        if (this.processCountDown < 1000 || !this.canFund()) {
          this.processCountDown = 0;
          this.processCountDownText = "Process";
        }

        this.processButton();
      };

      const handlerClaim = () => {
        this.claimCountDownText = null;

        if (this.userData.rewardBalance !== "0") {
          this.claimCountDown =
            new Date(this.userData.nextClaim * 1000) - new Date().getTime();
        }

        if (
          this.claimCountDown < 1000 ||
          this.userData.rewardBalance === "0" ||
          !this.claimStatus
        ) {
          this.claimCountDown = 0;
          this.claimCountDownText = "Claim";
        }
        this.claimButton();
      };

      const handlerCompound = () => {
        let nextClaim = this.userData.nextClaim * 1000;
        let userDelay = this.compoundDelay * 1000;

        this.compoundCountDownText = null;

        if (this.compoundingBalance !== 0 && this.compoundStatus !== "0") {
          this.compoundCountDown =
            new Date(nextClaim + userDelay) - new Date().getTime();
        }

        if (this.compoundCountDown < 1000 || this.compoundingBalance == 0) {
          this.compoundCountDown = 0;
          this.compoundCountDownText = "Compound";
        }
      };

      handlerProceess();
      this.processTimer = setInterval(handlerProceess, 1000);

      handlerClaim();
      this.claimTimer = setInterval(handlerClaim, 1000);

      handlerCompound();
      this.compoundTimer = setInterval(handlerCompound, 1000);
    },

    async connect() {
      this.menuOpen = false;
      if (this.isConnecting) {
        return;
      }
      try {
        await Web3.init();
        this.isConnecting = true;
      } catch (e) {
        console.log("Init: ", e);
      }
      this.isConnecting = false;
    },

    async disconnect() {
      this.menuOpen = false;
      this.active = 0;
      await Web3.disconnect();
    },

    numberFormat(x) {
      var value = parseFloat(x).toLocaleString("en-US", {
        minimumFractionDigits: 0,
        maximumFractionDigits: 9,
      });
      return value;
    },

    toHHMMSS(countDown) {
      let hours = Math.floor(
        (countDown % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      let minutes = Math.floor((countDown % (1000 * 60 * 60)) / (1000 * 60));
      let seconds = Math.floor((countDown % (1000 * 60)) / 1000);
      if (hours < 10) {
        hours = "0" + hours;
      }
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      return hours + ":" + minutes + ":" + seconds;
    },

    fundingReward() {
      let rewardAmount = 0;

      if (!this.requireFund) return rewardAmount;

      if (
        this.formatBalance(this.liquidityFund) >=
        this.formatBalance(this.requireFund)
      )
        rewardAmount += this.formatBalance(this.requireFund);

      if (
        this.formatBalance(this.treasuryFund) >=
        this.formatBalance(this.requireFund)
      )
        rewardAmount += this.formatBalance(this.requireFund);

      if (rewardAmount == 0) return rewardAmount;

      return (rewardAmount * this?.rateReward) / 100;
    },

    canFund() {
      let canFund =
        this.formatBalance(this.liquidityFund) >=
          this.formatBalance(this.requireFund) ||
        this.formatBalance(this.treasuryFund) >=
          this.formatBalance(this.requireFund);
      return canFund;
    },

    async processfund() {
      this.isLoadingFund = true;
      try {
        await Web3.processFund();
      } catch (e) {
        console.log("error", e);
      }
      this.isLoadingFund = false;
    },

    async claim() {
      this.isLoadingFund = true;
      try {
        await Web3.claim();
      } catch (e) {
        console.log("Claim", e);
      }
      this.isLoadingFund = false;
    },

    async compounduser() {
      this.isLoadingFund = true;
      try {
        await Web3.compoundUser();
      } catch (e) {
        console.log("CompoundUser: ", e);
      }
      this.isLoadingFund = false;
    },

    async massCompound() {
      this.isLoadingFund = true;
      try {
        await Web3.massCompound();
      } catch (e) {
        console.log("massCompound: ", e);
      }
      this.isLoadingFund = false;
    },

    async acceptRef() {
      this.isLoadingFund = true;
      try {
        await Web3.processReferrer();
      } catch (e) {
        console.log("AcceptReferral: ", e);
      }
      this.isLoadingFund = false;
    },

    async rejectRef() {
      try {
        await Web3.clearReferral();
      } catch (e) {
        console.log("DeclineReferral: ", e);
      }
    },

    copyRef() {
      const copyLink = ref(this.userRef);
      navigator.clipboard.writeText(copyLink.value);
      this.showTip = true;
      setTimeout(() => {
        this.showTip = false;
      }, 1000);
    },

    buy() {
      let buyLink =
        "https://pancakeswap.finance/swap?chain=bscTestnet&outputCurrency=0x6EF943EEA423e9E5346DfC2eE97bf22529b1c178";
      if (this.requireFund !== "0") {
        window.open(buyLink, "_blank");
      }
    },

    claimButton() {
      this.claimButtonStatus = true;
      if (
        this.formatBalance(this.userData.rewardBalance) === 0 ||
        this.formatBalance(this.userData?.inputsAmount) <
          this.formatBalance(this.rateReferral) ||
        this.userData?.nextClaim == 0 ||
        this.claimCountDown !== 0 ||
        this.isLoadingFund
      ) {
        this.claimButtonStatus = false;
      }
    },

    processButton() {
      this.processButtonStatus = true;
      if (
        (!this.formatBalance(this.requireFund) ||
          !this.canFund() ||
          this.fundingReward() == 0 ||
          this.isLoadingFund ||
          this.processCountDown !== 0 ||
          this.userData?.nextProcess === "0" ||
          !(this.formatBalance(this.requireFund) > 0) ||
          !(this.userData.inputsAmount >= this.rateReferral)) &&
        (!this.formatBalance(this.requireFund) ||
          !this.canFund() ||
          this.fundingReward() == 0 ||
          this.isLoadingFund ||
          !(this.formatBalance(this.requireFund) > 0) ||
          !this.excludeReward)
      ) {
        this.processButtonStatus = false;
      }
    },

    async tokenAdd() {
      try {
        await Web3.addToken();
      } catch (e) {
        return;
      }
    },

    formatBalance(balance, decimals) {
      if (!balance) {
        return "0";
      }
      return Utils.formatBalance(balance, decimals);
    },

    formatAddress(address) {
      return Utils.formatAddress(address);
    },

    formatDate(date) {
      return dateFormat(date, "yyyy/mm/dd HH:MM");
    },
  },
  computed: {
    ...mapGetters([
      "address",
      "balance",
      "staking",
      "reward",
      "total_reward",
      "isApproved",
      "claimedReward",
      "unClaimedReward",
      "liquidityFund",
      "treasuryFund",
      "fundThreshold",
      "referred",
      "userRef",
      "refPending",
      "referrerCount",
      "referredClaims",
      "rateReferral",
      "requireFund",
      "userData",
      "rateReward",
      "excludeReward",
      "canCompound",
      "compoundBalance",
      "compoundingBalance",
      "compoundDelay",
      "massCompoundReward",
      "rateCompound",
      "compoundStatus",
      "claimStatus",
      "rewardable",
    ]),
  },
  watch: {
    async address() {
      if (!this.address) {
        return;
      }
      // Todo: set default eligible false
    },
  },

  async mounted() {
    await Web3.initTimer();
    await Web3.init();
    await Web3.isReferred();

    Observer.$on(E_SIGNED, async () => {
      this.countDownTimer();
    });

    Observer.$on(E_REJECT_SIGN, async () => {
      this.isConnecting = false;
      // console.log("disconnected");
    });
  },
  data() {
    return {
      isConnecting: false,
      processCountDown: 0,
      claimCountDown: 0,
      compoundCountDown: 0,
      processCountDownText: null,
      claimCountDownText: null,
      compoundCountDownText: null,
      claimTimer: null,
      processTimer: null,
      compoundTimer: null,
      ModalActive: false,
      active: 0,
      isLoadingFund: false,
      showTip: false,
      claimButtonStatus: false,
      processButtonStatus: false,

      // Mobile Menu
      menuOpen: false,
    };
  },
};
</script>
