import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
const { formatBytes32String, parseBytes32String } = require("ethers/lib/utils");

import Web3Modal from "web3modal";

import Observer from "./Observer";
import store from "../store/index";
import {
  E_ACCOUNT_CHANGED,
  E_SIGNED,
  E_REJECT_SIGN,
} from "../constants/events";
import Config from "../config";
import Utils from "./Utils";
// import Parse from "./Parse";

import ABI from "../constants/abi";

const providerOptions = {
  walletconnect: {
    package: WalletConnectProvider, // required
    options: {
      // infuraId: Config.INFURA_ID, // required
      rpc: {
        [Config.CHAIN_ID]: Config.MAINNET_RPC,
      },
    },
  },
};

const web3Modal = new Web3Modal({
  cacheProvider: true,
  providerOptions, // required
});

class Web3Helper {
  constructor() {
    this.web3 = null;
    this.account = {};
    this.address = null;
    this.balance = 0;
    this.timer = null;
    this.provider = null;
    this.rewardBalance = 0;
    this.referrer = null;
    this.refset = null;
  }

  async disconnect() {
    try {
      web3Modal.clearCachedProvider();
    } catch (e) {
      return;
    }
    store.commit("SET", ["address", null]);
    this.address = null;
    clearInterval(this.timer);
    Observer.$emit(E_REJECT_SIGN);

  }

  async init() {
    let provider;
    const { cachedProvider } = web3Modal.providerController;

    // Stored connection
    if (cachedProvider) {
      try {
        provider = await web3Modal.connectTo(cachedProvider);
        this.provider = provider;
        this.web3 = new Web3(provider);
      }
      catch (e) {
        return;
      }
    }

    // No stored connection
    if (!cachedProvider) {
      try {
        provider = await web3Modal.connect();
        this.provider = provider;
        this.web3 = new Web3(provider);
      }
      catch (e) {
        return;
      }
    }

    this.initObserver();

    const accounts = await this.web3.eth.getAccounts();

    // Subscribe to accounts change
    provider.on("accountsChanged", (accounts) => {
      if (this.address === null) return;
      Observer.$emit(E_ACCOUNT_CHANGED, accounts);
    });

    // Subscribe to chainId change
    provider.on("chainChanged", async (chainId) => {
      // console.log('Chain Changed', chainId);
      // eslint-disable-next-line no-undef
      if (BigInt(chainId) != BigInt(Config.CHAIN_ID)) {
        this.switchNetwork();
      }
    });

    // Subscribe to provider connection
    provider.on("connect", (info) => {
      console.log("Chain Connected", info.chainId);
    });

    // Subscribe to provider disconnection
    provider.on("disconnect", (error) => {
      console.log("Wallet Disconnected", error.code, error.message);
    });

    Observer.$emit(E_ACCOUNT_CHANGED, accounts);
  }

  async addToken() {
    let perksAdded = localStorage.getItem("PERKS");
    if (!perksAdded) {
      try {
        perksAdded = await this.provider.request({
          method: "wallet_watchAsset",
          params: {
            type: "ERC20",
            options: {
              address: "0x6EF943EEA423e9E5346DfC2eE97bf22529b1c178",
              symbol: "Perks",
              decimals: 18,
              image: "http://localhost:8080/img/logo_64x64.png",
            },
          },
        });

        if (perksAdded) {
          localStorage.setItem("PERKS", true);
        } else {
          return;
        }

      } catch (e) {
        return;
      }
    }
  }

  async switchNetwork() {
    // eslint-disable-next-line no-undef
    const chainId = "0x" + BigInt(Config.CHAIN_ID).toString(16);
    if (this.isNetworkRequest) {
      return;
    }
    this.isNetworkRequest = true;
    try {
      await this.provider.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId }],
      });
    } catch (switchError) {
      // console.log('Switch Network Error', switchError);
      if (switchError.code === 4902) {
        try {
          await this.provider.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: chainId,
                chainName: "Binance Smart Chain Testnet",
                nativeCurrency: {
                  name: "tBNB ",
                  symbol: "tBNB", // 2-6 characters long
                  decimals: 18,
                },
                rpcUrls: [Config.MAINNET_RPC],
                blockExplorerUrls: [Config.EXPLORER_LINK],
              },
            ],
          });
        } catch (addError) {
          // handle "add" error
          // console.log('Add Network Error', addError);
        }
      }
    }
    this.isNetworkRequest = false;
  }

  initObserver() {

    Observer.$off(E_ACCOUNT_CHANGED);

    Observer.$on(E_ACCOUNT_CHANGED, async (accounts) => {
      this.address = accounts[0];

      const chainId = await this.web3.eth.getChainId();
      if (chainId != Config.CHAIN_ID) {
        await this.switchNetwork();
      }

      const balance = await this.getTokenBalance();
      this.balance = balance;

      const userData = await this.getTokenContract()
        .methods.userData(this.address)
        .call();
      this.rewardBalance = userData.rewardBalance;

      const excludeReward = await this.getTokenContract()
        .methods.excludeReward(this.address)
        .call();

      const rateReferral = await this.getTokenContract()
        .methods.rateReferral()
        .call();

      const referredClaims = await this.getTokenContract()
        .methods.referredClaims(this.address)
        .call();

      const referrerCount = await this.getTokenContract()
        .methods.referralsCount(this.address)
        .call();

      const compoundDelay = await this.getTokenContract()
        .methods.compoundDelay()
        .call();

      const massCompoundReward = await this.getTokenContract()
        .methods.massCompoundReward()
        .call();

      const rateCompound = await this.getTokenContract()
        .methods.rateCompound()
        .call();

      const compoundStatus = await this.getTokenContract()
        .methods.compoundStatus(this.address)
        .call();

      let claimStatus = false;
      claimStatus = !(
        Utils.formatBalance(userData.rewardBalance) === 0 ||
        Utils.formatBalance(userData?.inputsAmount) < Utils.formatBalance(rateReferral)
        || userData?.nextClaim == 0
      );

      store.commit("SET", ["rateReferral", rateReferral]);
      store.commit("SET", ["referredClaims", referredClaims]);
      store.commit("SET", ["referrerCount", referrerCount]);
      store.commit("SET", ["address", this.address]);
      store.commit("SET", ["balance", this.balance]);
      store.commit("SET", ["userData", userData]);
      store.commit("SET", ["excludeReward", excludeReward]);
      store.commit("SET", ["compoundDelay", compoundDelay]);
      store.commit("SET", ["massCompoundReward", massCompoundReward]);
      store.commit("SET", ["rateCompound", rateCompound]);
      store.commit("SET", ["compoundStatus", compoundStatus]);
      store.commit("SET", ["claimStatus", claimStatus]);

      Observer.$emit(E_SIGNED);
    });
  }

  async initOverview() {

    if (this.web3 === null) {
      try {
        this.web3 = new Web3(Config.MAINNET_RPC);
      } catch (e) {
        return
      }
    }

    const claimedReward = await this.getTokenContract()
      .methods.rewardClaimed()
      .call();
    const unClaimedReward = await this.getTokenContract()
      .methods.rewardUnclaimed()
      .call();
    const liquidityFund = await this.getTokenContract()
      .methods.liquidityFund()
      .call();
    const treasuryFund = await this.getTokenContract()
      .methods.treasuryFund()
      .call();
    const fundThreshold = await this.getTokenContract()
      .methods.perksLiquify()
      .call();
    const swapRouter = await this.getTokenContract()
      .methods.swapRouter()
      .call();
    const WETHAddr = await this.getRouterContract(swapRouter)
      .methods.WETH()
      .call();
    const rateReward = await this.getTokenContract()
      .methods.rateReward()
      .call();
    const pairAddress = await this.getTokenContract()
      .methods.swapPair()
      .call();

    const balanceBNB = await this.getWETHContract(WETHAddr)
      .methods.balanceOf(pairAddress)
      .call();

    let requireFund = 0;

    if (Utils.formatBalance(balanceBNB) > Utils.formatBalance(fundThreshold)) {
      try {
        requireFund = await this.getRouterContract(swapRouter)
          .methods.getAmountsIn(fundThreshold, [Config.TOKEN_ADDRESS, WETHAddr])
          .call();
        requireFund = requireFund[0];
      } catch (e) {
        requireFund = 0;
      }
    }

    store.commit("SET", ["claimedReward", claimedReward]);
    store.commit("SET", ["unClaimedReward", unClaimedReward]);
    store.commit("SET", ["liquidityFund", liquidityFund]);
    store.commit("SET", ["treasuryFund", treasuryFund]);
    store.commit("SET", ["fundThreshold", fundThreshold]);
    store.commit("SET", ["requireFund", requireFund]);
    store.commit("SET", ["rateReward", rateReward]);
  }

  initTimer() {
    if (this.timer) {
      clearInterval(this.timer);
    }

    const handler = async () => {
      await this.initOverview();
      await this.updateBalance();
      await this.checkCompound();
    };

    handler();
    this.timer = setInterval(handler, 10000);
  }

  async initAccount() {
    this.initTimer();
    Observer.$emit(E_SIGNED);
  }

  async updateBalance() {
    if (!this.address) return;

    this.isReferred();

    const balance = await this.getTokenBalance();

    this.balance = balance;

    const referredClaims = await this.getTokenContract()
      .methods.referredClaims(this.address)
      .call();

    const userData = await this.getTokenContract()
      .methods.userData(this.address)
      .call();

    this.rewardBalance = userData.rewardBalance;

    const compoundable = await this.getTokenContract()
      .methods.canCompound(this.address)
      .call();

    let canCompound = 0;
    let compoundBalance = 0;

    canCompound = compoundable[0];
    compoundBalance = Utils.formatBalance(compoundable[1]);

    const massCompoundReward = await this.getTokenContract()
      .methods.massCompoundReward()
      .call();

    const referrerCount = await this.getTokenContract()
      .methods.referralsCount(this.address)
      .call();

    const compoundStatus = await this.getTokenContract()
      .methods.compoundStatus(this.address)
      .call();

    const rateCompound = await this.getTokenContract()
      .methods.rateCompound()
      .call();

    const rateReferral = await this.getTokenContract()
      .methods.rateReferral()
      .call();

    let compoundingBalance = 0;
    let rewardbalance = Utils.formatBalance(this.rewardBalance);

    if (canCompound !== 0 && compoundBalance !== 0) {
      compoundingBalance = compoundBalance + rewardbalance;
    } else if (
      rewardbalance !== 0 &&
      compoundStatus !== '0'
    ) {
      compoundingBalance =
        (rewardbalance *
          rateCompound) /
        100 +
        rewardbalance;
    }

    let claimStatus = false;

    claimStatus = !(
      Utils.formatBalance(userData.rewardBalance) === 0 ||
      Utils.formatBalance(userData?.inputsAmount) < Utils.formatBalance(rateReferral)
      || userData?.nextClaim == 0
    );

    this.canReward(this.address, massCompoundReward);

    store.commit("SET", ["balance", balance]);
    store.commit("SET", ["userData", userData]);
    store.commit("SET", ["compoundingBalance", compoundingBalance]);
    store.commit("SET", ["massCompoundReward", massCompoundReward]);
    store.commit("SET", ["compoundStatus", compoundStatus]);
    store.commit("SET", ["referrerCount", referrerCount]);
    store.commit("SET", ["referredClaims", referredClaims]);
    store.commit("SET", ["claimStatus", claimStatus]);
  }

  async checkCompound() {

    if (!this.address) return;

    let canCompound = 0;
    let compoundBalance = 0;

    const compoundable = await this.getTokenContract()
      .methods.canCompound(this.address)
      .call();

    canCompound = compoundable[0];
    compoundBalance = Utils.formatBalance(compoundable[1]);

    store.commit("SET", ["canCompound", canCompound]);
    store.commit("SET", ["compoundBalance", compoundBalance]);
  }

  async loadReferrer() {
    if (!this.address) return;

    this.userLink();

    let ref = await this.getTokenContract()
      .methods.referrers(this.address)
      .call();

    let referred = Utils.formatBigInt(ref);
    if (referred === "0x0") {
      referred = "None";
      store.commit("SET", ["referred", referred]);
    } else {
      referred = Utils.formatAddress(Utils.formatChecksum(ref));
      store.commit("SET", ["referred", referred]);
    }
  }

  async isReferred() {
    if (!this.address) return;

    this.setReferrer();

    let referred = await this.getTokenContract()
      .methods.referrers(this.address)
      .call();

    if (Utils.formatBigInt(referred) === '0x0') {
      this.refset = false;

    } else {
      this.refset = true;
    }

  }

  setReferrer() {

    this.loadReferrer();

    let refPending = new URLSearchParams(window.location.search).get('ref');
    let refReferrer = this.getReferrerCookie();
    let address = this.address;

    refPending = refPending ? Utils.formatChecksum(refPending) : refPending;
    refReferrer = refReferrer ? Utils.formatChecksum(refReferrer) : refReferrer;
    address = address ? Utils.formatChecksum(address) : address;

    if (
      this.refset && refPending ||
      this.refset && refReferrer ||
      address == refPending ||
      address == refReferrer
    ) {
      this.clearReferral();
      return;
    }

    if (!refReferrer) {

      if (this.isAddress(refPending)) {
        this.referrer = refPending;
        localStorage.setItem("REFERRED", refPending);
        store.commit("SET", ["refPending", refPending]);
      }

    } else {
      store.commit("SET", ["refPending", refReferrer]);
      this.referrer = refReferrer;
    }

    this.canRefer();
  }

  getReferrerCookie() {
    let referredStored = localStorage.getItem("REFERRED");
    return (referredStored);
  }

  clearReferral() {
    localStorage.removeItem("REFERRED");
    let refPending;
    store.commit("SET", ["refPending", refPending]);
    window.history.replaceState({}, '', "/");
    this.loadReferrer();
  }

  userLink() {

    let userRef = window.location.protocol + '//' + window.location.host + '/?ref=';

    if (this.address) {
      userRef = userRef + Utils.formatChecksum(this.address);
    }

    store.commit("SET", ["userRef", userRef]);
  }

  async canRefer() {

    if (!this.address) return;
    if (!this.referrer) return;

    let referrer = Utils.formatChecksum(this.referrer);
    let referred = Utils.formatChecksum(this.address);

    const referrerInput = await this.getTokenContract()
      .methods.userData(referrer)
      .call();

    const referredInput = await this.getTokenContract()
      .methods.userData(referred)
      .call();

    const refReferrer = await this.getTokenContract()
      .methods.referrers(referrer)
      .call();

    const enableTrading = await this.getTokenContract()
      .methods.enableTrading()
      .call();

    const enableReferral = await this.getTokenContract()
      .methods.enableReferral()
      .call();

    const rateReferral = await this.getTokenContract()
      .methods.rateReferral()
      .call();

    const enableReward = await this.getTokenContract()
      .methods.enableReward()
      .call();

    let checkReferral = false;

    checkReferral = (
      Utils.formatBalance(referrerInput.inputsAmount) > Utils.formatBalance(referredInput.inputsAmount)) &&
      Utils.formatBalance(referrerInput.inputsAmount) >= Utils.formatBalance(rateReferral) &&
      Utils.formatChecksum(refReferrer) !== referred &&
      enableTrading && enableReward && enableReferral;

    if (!checkReferral) this.clearReferral();
  }

  async processReferrer() {
    this.web3 = new Web3(window.ethereum);
    this.setReferrer();

    if (!this.referrer)
      return;

    await this.getTokenContract().methods.referralAccept(this.referrer).send({
      from: this.address,
    });

    this.clearReferral();
  }

  async getTokenBalance() {
    let web3;
    if (web3 === undefined) {
      try {
        web3 = new Web3(new Web3.providers.HttpProvider(Config.MAINNET_RPC));
        const tokenContract = new web3.eth.Contract(
          ABI.PERKSABI,
          Config.TOKEN_ADDRESS
        );
        const balance = await tokenContract.methods.balanceOf(this.address).call();
        return balance;

      } catch (error) {
        return;
      }
    }
  }

  async canReward(address, amount) {
    let rewardable = await this.getTokenContract()
      .methods.canReward(address, amount)
      .call();
    store.commit("SET", ["rewardable", rewardable]);
  }

  sign(address, nonce) {
    const message = this.web3.eth.accounts.hashMessage(
      `I am signing my one-time nonce: ${nonce}`
    );
    return this.web3.eth.sign(message, address);
  }

  async processFund() {
    this.web3 = new Web3(window.ethereum);

    await this.getTokenContract().methods.processFunding().send({
      from: this.address,
    });
    await this.initOverview();
    await this.updateBalance();
  }

  async compoundUser() {
    this.web3 = new Web3(window.ethereum);

    await this.getTokenContract().methods.compoundUser(this.address).send({
      from: this.address,
    });
    await this.initOverview();
    await this.updateBalance();
  }

  async massCompound() {
    this.web3 = new Web3(window.ethereum);

    await this.getTokenContract().methods.massCompound().send({
      from: this.address,
    });
    await this.initOverview();
    await this.updateBalance();
  }

  async claim() {
    this.web3 = new Web3(window.ethereum);

    await this.getTokenContract()
      .methods.claimReward()
      .send({
        from: this.address,
      });

    await this.initOverview();
    await this.updateBalance();
  }

  getTokenContract(address) {
    if (!address) {
      address = Config.TOKEN_ADDRESS;
    }
    return new this.web3.eth.Contract(
      ABI.PERKSABI,
      Web3.utils.toChecksumAddress(address)
    );
  }

  getRouterContract(address) {
    return new this.web3.eth.Contract(
      ABI.ROUTERABI,
      Web3.utils.toChecksumAddress(address)
    );
  }

  getWETHContract(address) {
    return new this.web3.eth.Contract(
      ABI.WETHABI,
      Web3.utils.toChecksumAddress(address)
    );
  }

  bytes32(str) {
    return formatBytes32String(str);
  }

  parseBytes32(bytes) {
    return parseBytes32String(bytes);
  }

  isAddress(address) {
    return this.web3.utils.isAddress(address);
  }

  async getBalance(address) {
    return this.web3.eth.getBalance(address);
  } 

  async decimals(address) {
    if (!address) {
      address = Config.TOKEN_ADDRESS;
    }
    const tokenContract = this.getTokenContract();
    return parseInt(await tokenContract.methods.decimals().call());
  }

  async getGasPrice() {
    return await this.web3.eth.getGasPrice();
  }

  async estimateGasLimit(option) {
    // from, to, data, value
    return await this.web3.eth.estimateGas(option);
  }

  async send(transaction, privateKey, options) {
    // from, to, gas: gasLimit, value, gasPrice / (maxFeePerGas, maxPriorityFeePerGas)
    if (transaction) {
      options.data = transaction.encodeABI();
    }
    const signedTx = await this.web3.eth.accounts.signTransaction(
      options,
      privateKey
    );

    const result = await this.web3.eth.sendSignedTransaction(
      signedTx.rawTransaction
    );
    result.hash = result.blockHash;
    return result;
  }
}

const helper = new Web3Helper();
// helper.init();
export default helper;
